@use 'variables' as v;

h1, h2, h3 {
	font-family: v.$principalFont;
	margin: 0;
	font-weight: 400;
	color: v.$white;
	text-transform: uppercase;
	text-align: center;
}

h1 {
	font-size: 4rem;
}
h2 {
	font-size: 3rem;
}
h3 {
	font-size: 2.4rem;
}
p {
	color: v.$gray;
	font-size: 2rem;
}