/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  min-height: 100%;
  color: #FFFFFF;
  font-family: "Roboto", sans-serif;
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  width: 85%;
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  margin: 5rem auto;
  padding-top: 12rem;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

ul, li {
  list-style: none;
}

img {
  max-width: 100%;
  width: inherit;
  height: inherit;
}

#bg {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #16222A;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #162f3a, #0d151a);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #162f3a, #0d151a);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-weight: 400;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  color: #e1e1e1;
  font-size: 2rem;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  z-index: 1;
  transition: all 0.7s ease;
}
.header .logo-container .logo {
  width: 25rem;
  height: 10rem;
}
.header .logo-container .logo rect {
  transition: stroke 0.3s ease;
}
@media (max-width: 480px) {
  .header .logo-container .logo {
    width: 20rem;
    height: 10rem;
  }
}
.header .logo-container .logo .logoPath {
  fill: #FFFFFF;
}
.header .menu-icon {
  font-size: 4rem;
  display: none;
  z-index: 2;
}
@media (max-width: 768px) {
  .header .menu-icon {
    display: inline-block;
  }
}

.down {
  background-color: #3A6073;
  padding: 0rem 2rem;
}
.down .logo rect {
  stroke: #FFFFFF;
}

.nav ul {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.nav ul li a {
  color: #FFFFFF;
  padding: 2rem;
  font-size: 2rem;
  position: relative;
  display: block;
  z-index: 99;
  transition: all 0.3s ease;
}
.nav ul li a:before {
  content: "";
  display: block;
  width: 0%;
  height: 0.3rem;
  background-color: #FFFFFF;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: all 0.3s ease;
}
.nav ul li a:hover {
  cursor: pointer;
  color: #6396b0;
}
.nav ul li a:hover:before {
  width: 100%;
}
@media (max-width: 768px) {
  .nav {
    background-color: #3A6073;
    transform: translate(-100%);
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease;
  }
  .nav ul {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5rem;
  }
  .nav.active {
    transform: translate(0);
  }
}

.cards .title-cards-container {
  text-align: center;
}
.cards .paragraph-cards-container {
  text-align: center;
  margin-bottom: 3rem;
}
.cards .cards-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
}
@media (max-width: 768px) {
  .cards .cards-container {
    grid-template-columns: 1fr;
  }
}
.cards .cards-container .card {
  padding: 2rem;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(0.2rem);
  border-radius: 1rem;
}
.cards .cards-container .card .card-icon {
  font-size: 10rem;
}
.cards .cards-container .card .card-title {
  margin: 2rem 0;
}
.cards .cards-container .card .card-text p {
  font-size: 1.5rem;
}

.footer {
  background-color: #000000;
  color: #e1e1e1;
}
.footer .footer-container {
  padding: 3rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20rem;
}
@media (max-width: 768px) {
  .footer .footer-container {
    padding: 1rem;
    grid-template-columns: 1fr;
    row-gap: 4rem;
  }
}
@media (max-width: 480px) {
  .footer .footer-container.container {
    width: 95%;
  }
}
.footer .footer-container h3 {
  color: #FFFFFF;
  text-align: start;
  font-size: 2rem;
}
.footer .footer-container .contact .contact-group a {
  width: auto;
  display: flex;
  align-items: center;
  color: #FFFFFF;
  font-size: 1.6rem;
  transition: all 0.3s ease;
}
@media (max-width: 320px) {
  .footer .footer-container .contact .contact-group a {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.footer .footer-container .contact .contact-group a p {
  margin: 0.7rem;
}
@media (max-width: 320px) {
  .footer .footer-container .contact .contact-group a p {
    font-size: 1.3rem;
  }
}
.footer .footer-container .contact .contact-group a:hover {
  color: #3A6073;
}
.footer .footer-container .contact .contact-group a:hover i {
  color: #3A6073;
  transform: scale(1.2);
}
.footer .footer-container .other-pages ul li {
  list-style: disc;
  transition: color 0.3s ease;
}
.footer .footer-container .other-pages ul li a {
  color: #FFFFFF;
  font-size: 1.6rem;
  transition: color 0.3s ease;
}
.footer .footer-container .other-pages ul li:hover {
  color: #3A6073;
}
.footer .footer-container .other-pages ul li:hover a {
  color: #3A6073;
}
.footer .copyright {
  text-align: center;
  width: 100%;
  padding: 2rem 0;
  color: #FFFFFF;
}
.footer .copyright p {
  font-size: 1.8rem;
}

.about {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-justify: distribute;
  margin-top: 0;
  padding-top: 15rem;
}
@media (max-width: 768px) {
  .about {
    text-align: center;
  }
}
.about .about-text-container {
  flex: 4;
}
.about .about-text-container h1 {
  font-size: 4rem;
  text-align: start;
}
.about .about-text-container p {
  font-size: 3rem;
}
@media (max-width: 480px) {
  .about .about-text-container p {
    font-size: 2rem;
  }
}
.about .about-text-container .typed {
  color: #0da6f3;
}
.about .about-img-container {
  max-width: 30rem;
}
@media (max-width: 768px) {
  .about .about-img-container {
    display: none;
  }
}
.about .about-img-container img {
  width: 100%;
  height: auto;
}

.proyects h2 {
  padding-bottom: 3rem;
}
.proyects .proyects-container .proyect {
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}
@media (max-width: 768px) {
  .proyects .proyects-container .proyect {
    grid-template-columns: 1fr;
  }
}
.proyects .proyects-container .proyect .text-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 2rem;
}
.proyects .proyects-container .proyect .text-container h3 {
  text-align: start;
}
.proyects .proyects-container .proyect .text-container .visit-site-button {
  background-color: #3A6073;
  padding: 1rem 3rem;
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin: 1rem 0rem;
  border-radius: 2.2rem;
}
@media (max-width: 768px) {
  .proyects .proyects-container .proyect .text-container .visit-site-button {
    width: auto;
  }
}
.proyects .proyects-container .proyect .text-container .visit-site-button:hover {
  background-color: #294451;
  cursor: pointer;
}
.proyects .proyects-container .proyect .text-container .view-proyect-button {
  background-color: #FFFFFF;
  padding: 1rem 3rem;
  color: #3A6073;
  font-size: 2rem;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin: 1rem 0rem;
  border-radius: 2.2rem;
}
@media (max-width: 768px) {
  .proyects .proyects-container .proyect .text-container .view-proyect-button {
    width: auto;
  }
}
.proyects .proyects-container .proyect .text-container .view-proyect-button:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}

.contact-us {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 10rem;
  margin-bottom: 5rem;
}
.contact-us .contact-us-container {
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(0.2rem);
  border-radius: 3rem;
  padding: 5rem;
  text-align: center;
}
.contact-us .contact-us-container .contact-button {
  background-color: #3A6073;
  padding: 1rem 3rem;
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin: 1rem 0rem;
  border-radius: 2.2rem;
  font-size: 3rem;
}
@media (max-width: 768px) {
  .contact-us .contact-us-container .contact-button {
    width: auto;
  }
}
.contact-us .contact-us-container .contact-button:hover {
  background-color: #294451;
  cursor: pointer;
}