@use 'base/variables' as v;
@use 'base/mixins' as m;

.cards{
  .title-cards-container{
    text-align: center;
  }
  .paragraph-cards-container{
    text-align: center;
    margin-bottom: 3rem;
  }
  .cards-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;

    @include m.tablet{
      grid-template-columns: 1fr;
    }

    .card{
      padding: 2rem;
      text-align: center;
      background-color: rgba($color: #ffffff, $alpha: .2);
      backdrop-filter: blur(.2rem);
      border-radius: 1rem;

      .card-icon{
        font-size: 10rem;
      }

      .card-title{
        margin: 2rem 0;
      }

      .card-text{
        p{
          font-size: 1.5rem;
        }
      }
    }
  }
}